<template>
    <div class="shop">
        <navigation type="true"></navigation>
        <div class="shop-main">
             <div class="container">
                  <div class="shop-info">
                     <img  :src="logoUrl(restaurant.image_path)"  alt="">
                     <div class="shop-info-wrapper">
                         <h1>{{restaurant.name}}</h1>
                          <div class="shopguide-info-rate">
                               <div class="starrating  icon-star">
                                   <span class="icon-star" :style="{width:restaurant.rating*20+'%'}"></span>
                               </div>
                               ( <span>{{restaurant.rating_count}}</span> )
                               <span>{{restaurant.recent_order_num}}</span>
                          </div>
                     </div>

                  </div>
                  <div class="shopguide-server">
                       <span><em></em><em class="shopguide-server-value">35</em></span>
                       <span><em></em><em class="shopguide-server-value">4</em></span>
                       <span><em></em><em class="shopguide-server-value">38</em></span>
                  </div>
                  <!-- <a class="shopguide-favor" href="javascript:">
                       <i class="icon-favorite"></i>
                       <span ></span>
                  </a> -->
             </div>
        </div>

    </div>
</template>

<script>
 import {logoUrl} from '@/common/js/throttle.js'
 import navigation from '@/components/navigation/navigation.vue';
 import axios from 'axios';
 import Vue from 'vue'

  export default {
       data() {
          return {
             restaurant:'',
             logoUrl:logoUrl
          }
       },
       components:{
           navigation
       },
       computed:{
           restaurantId() {
              return this.$route.params.id
           }

       },

       created:function() {

            var url = '//mainsite-restapi.ele.me/shopping/restaurant/'+this.restaurantId+'?extras%5B%5D=activities&extras%5B%5D=license&extras%5B%5D=identification&//extras%5B%5D=albums&extras%5B%5D=flavors&latitude=30.18773&longitude=120.16047';
           axios.get(url).then((data) => {

                    this.restaurant = data.data;
                    console.log(this.restaurant)

           })
       }

  }

</script>
<style lang="scss">
    .shop {
         .shop-main {
              padding-top:48px;
              background:url(./shop-bg.0391dd.jpg);
              .container {
                   width:1180px;
                   height:142px;
                   display: table;
                   box-sizing:border-box;
                   position:relative;
                   .shop-info {
                    display: table-cell;
                    vertical-align: middle;
                    color: #FFFFFF;

                      img {
                          margin-right: 15px;
                          width: 95px;
                          height: 95px;
                          border-radius: 50%;
                          vertical-align: middle;
                      }
                      .shop-info-wrapper {
                           display: inline-block;
                           vertical-align: middle;
                           h1 {
                               font-size: 20px;
                               max-width: 270px;
                           }
                           .shopguide-info-rate {
                                margin: 10px 0;
                           }

                      }

                    }
                     .shopguide-server {
                          width:700px;
                          display: table-cell;
                          padding-right: 100px;
                          vertical-align: middle;
                          color: #fff;
                          text-align: right;
                          span {
                              display: inline-block;
                              vertical-align: top;
                              text-align: center;
                              margin-left: 80px;
                              .shopguide-server-value {
                                  display: block;
                                  margin-top: 12px;
                                  margin-bottom: 3px;
                                  font-size: 18px;
                              }
                          }
                      }
                      .shopguide-favor {
                          position: absolute;
                          top: 0;
                          right: 0;
                          padding: 10px 5px;
                          width: 80px;
                          text-align: center;
                          background: #FFF;
                          background: rgba(255,255,255,.1);
                          border-radius: 0 0 5px 5px;
                          color: #eee;
                          text-align:center;
                           i {
                              font-size:20px;

                           }
                           span {
                               display: block;
                           }
                      }
              }
         }
    }
</style>


