import axios from 'axios';

export default {
    getAjax({commit,state},data) {

        if(state.rstCategoriesId) {
            data = '&restaurant_category_ids%5B%5D='+state.rstCategoriesId;
        }
        var url = 'https://mainsite-restapi.ele.me/shopping/restaurants?extras%5B%5D=activities&geohash=wtmkw85zj0w&latitude=30.32347&limit=24&longitude=120.17383&terminal=web&'+data;
        axios.get(url).then((data) => {
            if(data.statusText=="OK") {
               commit('setrestaurants',data.data)
            }
       })

    }
}


