import home from '@/components/home/home'
import place from '@/components/place/place'
import shop from '@/components/shop/shop.vue'




 const routes = [
         {
            path:'/home',
            component:home
         },
         {
            path:'/place',
            component:place
         },
         {
            path:'/shop/:id',
            component:shop
         },
         {
            path:'/',
            redirect:'/place'
         }



]
export default routes


