<template>
     <div class="place" >
        <navigation></navigation>
        <div class="main">
           <search></search>
           <store-list></store-list>
           <store-block></store-block>
           <div class="more" @click="more">
              ...
           </div>
        </div>
    </div>
</template>
<script >
import search from '@/components/place/search/search.vue'
import storeList from '@/components/place/store-list/store-list'
import storeBlock from '@/components/place/store-block/store-block'
import navigation from '@/components/navigation/navigation.vue'
import {Throttle} from '@/common/js/throttle.js'
export default {
     data() {
        return {
            offset:0,
            lock:false,

        }
     },
    components:{
        search,
        storeList,
        storeBlock,
        navigation
    },
    methods:{
        scrollDate() {
             this.offset+=24;
                console.log(this.$store.getters.getMaxOffset)
                if(this.offset>this.$store.getters.getMaxOffset) {
                   this.offset-=24;
                   return
                }
               this.$store.commit('setIsScroll',true)
              this.$store.dispatch('getAjax','offset='+this.offset)
        },
        more() {
            this.$store.commit('setMaxOffset',4);
            this.scrollDate();
        }
    },
    created() {
       window.addEventListener('scroll',() => {

           Throttle(this.scrollDate);
       })
   console.log(this.$route.matched[0].path)
    }
}



</script>
<style lang="scss">
    .place {

     .main {
         width:1180px;
         margin: 0 auto;
         .more {
             line-height: 3;
             text-align: center;
             background-image: linear-gradient(to bottom,#f9f9f9,#eee);
             font-size: 18px;
             cursor: pointer;
             color: #777;
        }
     }

 }
</style>


